/* NOT COLOURED */
.not-coloured{
    padding: 3% 7%;
}
/* ABOUT */
.timeline{

    position:relative;
    margin:1rem auto;
}
.timeline:after{
    content: " ";
    position:absolute;
    background-color: #861657;
    background-image :linear-gradient(326deg, #861657 0%, #ffa69e 74%);
    width:10px;
    top:0;
    bottom:0;
    left:50%;
    margin-left: -3px;
}
.card{
    width: 50%;
    margin: 0px;
    padding: 10px 30px;
}

.left{
    left:0;
}
.right{
    left:50%
}
.card-body{
    background-color: #f5f7fa;
    position: relative;
    padding: 20px;
    border-radius: 6px;
}
.social-logo{
    width: 5rem;
    padding: 1rem;
    margin: auto;
}
@media screen and (max-width:700px){
    .timeline::after{
        left:2%;
    }
    .card{
        width: 100%;
        /* margin: 1.5rem 0 0 0; */
    }
    .left, .right{
        left:2%;
    }
}
@media screen and (max-width:550px){
    .card{
        width: 85%;
        margin: 1.5rem 0 0 0;
    }
    .left, .right{
        left:1%;
    }
}
@media screen and (max-width:450px){
    .card{
        width: 75%;
        margin: 1.5rem 0 0 0;
    }
}
